.box-container{
  display:flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;

}
.box-container .card{
  width:350px;
  height:250px;
  background: linear-gradient(180deg,#008aff,#ff137d);
  color:white;
  margin:10px;
  text-align: center;
  border-radius: 10px;
}



.button-group{
  text-align: center;

}
.button-group .btn1{
background: coral;
margin: 10px 40px;
padding:10px;
color:white;
border: none;
outline: none;
border-radius: 10px;
font-size: 18px;
cursor: pointer;


}
.btn1:hover{
  background-color: crimson;
  transition: 2s linear ease-in-out;
}
.heading{
  text-align: center;
  color: crimson;
  font-weight: 700;
}